let AppComponent = class AppComponent {
    constructor(platform, splashScreen, statusBar, authServices, router, menu) {
        this.platform = platform;
        this.splashScreen = splashScreen;
        this.statusBar = statusBar;
        this.authServices = authServices;
        this.router = router;
        this.menu = menu;
        this.initializeApp();
        this.authServices.userData$.subscribe((res) => {
            this.authUser = res;
        });
    }
    initializeApp() {
        this.platform.ready().then(() => {
            this.statusBar.styleDefault();
            this.splashScreen.hide();
        });
    }
    navigateToLoginPage() {
        // router naviage to login page
        this.router.navigate(['home/feed']);
        this.closeMenu();
    }
    closeMenu() {
        this.menu.close();
    }
    logoutAction() {
        this.authServices.logout();
        //window.location.href="/login";
    }
    goSettingAction() {
        this.router.navigate(['home/settings']);
        this.closeMenu();
    }
    goHelpAction() {
        this.router.navigate(['home/help']);
        this.closeMenu();
    }
    gotProfileAction() {
        this.router.navigate(['home/messages']);
        this.closeMenu();
    }
    gotoWorkReportAction() {
        this.router.navigate(['home/workreport']);
        this.closeMenu();
    }
    gotoPagesAction(urls) {
        this.router.navigate([urls]);
        this.closeMenu();
    }
};
